<template>
	<div id="detail">
		<div class="banner">
			<mt-swipe :auto="5000">
				<mt-swipe-item v-for="src in dt_list">
					<img :src="src">
				</mt-swipe-item>
			</mt-swipe>
			<span @click="back()"><img src="../../assets/img/arrow-left.png"></span>
		</div>
		<div class="num">
			<span class="price">{{price}}</span><span class="cost"><del>{{cost}}</del>{{discount}}</span>
			<div class="rt">
				<img src="../../assets/img/kuaiqiang.png">
				<span>{{hot}}</span>
			</div>
		</div>
		<p class="title">{{title}}<span>{{brand}}</span></p>
		<div class="zheng">
			<img src="../../assets/img/zheng.png">100%    
			<i></i>
		</div>
		<div class="color">
			<dl>
				<dt v-if="color_list==undefined?hidden:show"></dt>
				<dd v-for="(item,index) in color_list" :class="{checked:msg==index}" @click="change(index,item)">
					<img :src="item.src">
					{{item.con}}
				</dd>
				<dt></dt>
				<dd class="checked">{{size_list}}</dd>
			</dl>
		</div>
		<div class="parm">
			<span></span>
			<span></span>
		</div>
		<div class="distr">
			<div class="f1">
				<span></span>
				<span>
					...
					<p>927()</p>
				</span>
			</div>
			<div class="f2">
				<span></span>
				<span>38</span>
			</div>
		</div>
		<div class="services">
			<ul>
				<li><i></i></li>
				<li><i></i></li>
				<li><i></i>7</li>
				<li><i></i></li>
				<li><i></i></li>
			</ul>
		</div>
		<div class="brand">
			<img :src="brand_src">
			<p>{{brand}}</p>
			<p>{{brand_desc}}</p>
			<button></button>
		</div>
		<div class="see">
			
		</div>
		<div class="qa">
			<span></span><span></span>
			<p><i>Q</i></p>
			<p><i>A</i></p>
		</div>
		<div class="post">
			<h1></h1>
			<button><img src="../../assets/img/earphone.png"></button>
		</div>
		<div class="detail">
			<h1></h1>
			<ul>
				<li v-for="src in xq_list">
					<img :src="src">
				</li>
			</ul>
		</div>
		<div class="services_1">
			<img src="../../assets/img/server.png">
		</div>
		<div class="price_sm">
			<h1></h1>
			<p>
				/
				
				
				
			</p>
		</div>
		<div class="bottom">
			  
		</div>
		<footer>
			<div class="con">
				<div class="lf">
					<router-link to="/car">
						<img src="../../assets/img/gouwuche.png"><br>
						
						<span v-show="this.$store.state.count>0">{{this.$store.state.count}}</span>
					</router-link>
				</div>
				<div class="rt">
					<ul>
						<li>
							<span @click="add()">{{cost}}</span>
						</li>
						<li>
							<span @click="add()">{{price}}</span> >
						</li>
					</ul>
				</div>
			</div>
		</footer>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				show:true,
				hidden:false,
				msg:0,
				dt_list:[],
				xq_list:[],
				list:[],
				price:'',
				cost:'',
				discount:'',
				hot:'',
				title:'',
				brand:'',
				color_list:[],
				color:'',
				size_list:[],
				brand_src:'',
				brand_desc:'',
				id:this.$route.params.id
			}
		},
		mounted(){
			//console.log(this.$route.params.id);
			this.$http.get('./data/product.json')
			.then((response)=> {
				this.list=response.data.list;
				//console.log(this.list.length);
				for(var i=0,y=1; i<this.list.length; i++,y++){
					if(this.$route.params.id==y){
						this.dt_list=response.data.list[i].dt_list;
						this.xq_list=response.data.list[i].xq_list;
						this.price=response.data.list[i].price;
						this.cost=response.data.list[i].cost;
						this.discount=response.data.list[i].discount;
						this.hot=response.data.list[i].hot;
						this.title=response.data.list[i].title;
						this.brand=response.data.list[i].brand;
						this.color_list=response.data.list[i].color_list;
						this.size_list=response.data.list[i].size_list;
						this.brand_src=response.data.list[i].brand_src;
						this.brand_desc=response.data.list[i].brand_desc;
					}
				}
			})
		},
		methods:{
			back(){
				this.$router.go(-1);
			},
			change(a,b){
				this.msg=a;
				this.color=b.con;
			},
			add(){
				const product={};
				product.src=this.dt_list[0];
				product.title=this.title;
				product.price=this.price;
				product.brand=this.brand;
				product.id=this.id;
				product.color=this.color;
				product.size=this.size_list;
				product.count=1;
				if((product.color=='')&&(this.color_list!=undefined)){
					product.color=this.color_list[0].con;
				};
				this.$store.commit("addToCar",product)
			}
		}
	}
</script>
<style scoped>
#banner .mint-swipe-items-wrap{padding-bottom:100%}
</style>


