<template>
	<div id="car">
		<h1><span @click="change()"><img src="../../assets/img/arrow-r.png"></span>{{this.num==0?"":""}}</h1>
		<div class="con" v-if="this.num==0">
			<img src="../../assets/img/car.png"><br>
			<br>
			<router-link to="/product_list">
				<button></button>
			</router-link>
		</div>
		<div class="con_list" v-if="this.num>0">
			<div class="safe">
				<img src="../../assets/img/safe.jpg">
			</div>
			<div class="address">
				<p> 134*****381<br>
				520<br>
				
				<span><img src="../../assets/img/arrow-left.png"></span>
				</p>
				<hr/>
				<p>{{num}}/<span><img src="../../assets/img/arrow-left.png"></span></p>
			</div>
			<ul>
				<h2></h2>
				<li v-for="(item,index) in this.$store.state.car">
					<img :src="item.src">
					<p class="title">
						<span class="t_left">{{item.title}}</span>
						<span class="price">{{item.price}}</span>
					</p>
					<p class="color"><span v-if="item.color!=''">{{item.color}}</span>{{item.size}}</p>
					<p class="post"><span></span><span></span></p>
					<i @click="cutCount(index)">-</i><span class="count">{{item.count}}</span><i @click="addCount(index)">+</i>
					<button type="text" class="btn" @click="del"></button>
				</li>
				<p class="sub">{{total.toFixed(2)}}</p>
			</ul>
			<div class="youhui">
                             
			</div>
			<div class="sum">
				<p><span>{{(total+fare).toFixed(2)}}</span></p>
				<p><span>{{total.toFixed(2)}}</span></p>
				<p><span>{{fare}}</span></p>
			</div>
			<div class="fapiao">
				<span><img src="../../assets/img/arrow-r.png"></span>
			</div>
			<footer>
				<span>{{(total+fare).toFixed(2)}}</span><br>
				<button></button>
			</footer>
		</div>
	</div>
</template>
<script>
	import { MessageBox } from 'mint-ui';
	export default{
		data(){
			return{
				num:this.$store.state.car.length,
				sub:0,
				fare:5
			}
		},
		computed:{
			total(){
				let price=0;
				for(let i=0; i<this.$store.state.car.length; i++){
					price+=this.$store.state.car[i].price*this.$store.state.car[i].count;
				}
				return price;
			}
		},
		methods:{
			change(){
				this.$router.go(-1);
			},
			count(){
				let count=0;
				for(let i=0; i<this.$store.state.car.length; i++){
					count+=this.$store.state.car[i].count;
				}
				this.$store.commit("addCount",count)
			},
			addCount(a){
				this.$store.state.car[a].count++;
				this.count();
			},
			cutCount(a){
				if(this.$store.state.car[a].count>1){
					this.$store.state.car[a].count--
				};
				this.count();
			},
			del(index){
				MessageBox.confirm('?').then(action => {
					this.$store.state.car.splice(index,1);
					this.num--;
					this.count();
				});
			}
		}
	}
</script>
<style>
body:{background:#f3f4f5;}
</style>


