<template>
	<div id="login">
		<div class="top">
			<img src="../../assets/images/logo.png">
			<router-link to="/">
				<span class="mlogin-close-in"></span>
			</router-link>
		</div>
		<div class="login" v-if="mobile">
			<ul>
				<li>
					<label for="mobile"></label>
					<input type="tel" id="mobile" placeholder="">
				</li>
				<li>
					<label for="authcode"></label>
					<input type="tel" id="authcode" placeholder="">
					<button class="btn01"></button>
				</li>
			</ul>
			<a class="btn02"></a>
			<p><i :class="{agreement:show}" @click="checked()"></i><a href=""></a><a href=""></a><a href=""></a></p>
		</div>
		<div class="login" v-if="password">
			<ul>
				<li>
					<label for="mobile"></label>
					<input type="tel" id="mobile" placeholder="">
				</li>
				<li>
					<label for="authcode"></label>
					<input type="password" id="authcode" placeholder="">
				</li>
			</ul>
			<a class="btn02"></a>
			<p><a href="" style="float:right; font-size:14px"></a></p>
		</div>
		<div class="line"><span></span></div>
		<div class="pass"><button class="btn03" @click="login()"></button></div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				show:false,
				mobile:true,
				password:false
			}
		},
		methods:{
			checked(){
				this.show=!this.show;
			},
			login(){
				this.mobile=!this.mobile;
				this.password=!this.password;
				if(this.mobile==true){
					$(".btn03").html("");
				}else{
					$(".btn03").html("");
				};
			}
		}
	}
</script>


